@import "variables";

$lightSize: 100px;

.block-lights{
	height: $pipScreenHeight - $pipToolbar;
	background: #232323;
	position: absolute;
	left: 0;
	width: 138px;

	.lights{
		padding: 0;
		margin: 0;
		list-style: none;

		li{
			display: inline-block;
		}

		.light{
			border-radius: 50%;
			border: $pipBorderWidth solid $pipBorderColor;
			width: $lightSize;
			height: $lightSize;
			display: inline-block;
			margin: 28px 0 8px 15px;

			&.off{
				opacity: 0.8;
			}

			&.on{
				box-shadow: 0px 0px 51px;
			}

			.name, .status{
				text-align: center;
			}

			.name{
				margin-top: 36px;
				text-shadow: 0px 0px 9px black;
				line-height: 10px;
				color: white;
			}

			.status{
				font-size: smaller;
				text-shadow: 0px 0px 9px black;

				color: rgba(255, 255, 255, 0.76);
			}
		}
	}


}

